@font-face {
    font-family: 'Romic';
    font-style: normal;
    src: local('Romic'), local('Romic'),
        url(/got-scoreboard/static/media/Romic.caba7a56.ttf) format('truetype');
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Romic';
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-image: url(data:image/png;base64,);
    background-repeat: repeat;
}

.overlay {
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    background-color: rgba(0, 0, 0, 0.75);
}

.modal {
    color: #b1d5f4;
    background-image: url(data:image/png;base64,);
    background-repeat: repeat;
    position: absolute;
    top: 50%;
    left: 50%;
    right: auto;
    bottom: auto;
    border: 1px solid rgb(204, 204, 204);
    overflow: auto;
    outline: none;
    padding: 20px;
    margin-right: -50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}


